@font-face {
    font-family: 'Trajan, Trajan';
    src: url('../assets/fonts/TrajanPro-Bold.otf') format('opentype');
}

@font-face {
    font-family: 'Pretendard, Thin';
    src: url('../assets/fonts/Pretendard-Light.otf') format('opentype');
}

@font-face {
    font-family: 'Pretendard, Thick';
    src: url('../assets/fonts/Pretendard-SemiBold.otf') format('opentype');
}

@font-face {
    font-family: 'TheBold, TheBold';
    src: url('../assets/fonts/theboldfont.ttf');
}

body {
    background-color: #181a1f;
    color: white;
}

.match-fetcher {
    margin-top: 2%;
}

.match-fetcher > h2 {
    text-align: center;
    margin-top: 20px;
    color: white;
}

#search {
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
}
  
#search > label {
    width: 100%;
}

#search-riot-id {
    width: 100%;
    font-family: 'Pretendard, Thin';
    font-size: 16px;
    border: 3px solid #00B4CC;
    border-right: none;
    padding: 5px;
    height: 20px;
    border-radius: 5px 0 0 5px;
    outline: none;
    color: #9DBFAF;
}

#search-riot-id:focus{
    color: #00B4CC;
}

#search-button {
    width: 40px;
    height: 36px;
    border: 1px solid #00B4CC;
    background: #00B4CC;
    text-align: center;
    color: #fff;
    border-radius: 0 5px 5px 0;
    cursor: pointer;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

#search-button > img {
    width: 40px;
    filter: invert(100%);
}

.match-fetcher{
    width: 500px;
    position: absolute;
    top: 110%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.banner {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
}

.banner > img {
    min-width: 100%;
    filter: blur(4px);
}

.title {
    padding-top: 15%;
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.title > h1, .title > img {
    filter: drop-shadow(2px 4px 6px black);
}

.title > h1 {
    text-align: center;
    color: white;
    font-size: 80px;
    font-family: 'Pretendard, Thick';
}

#tac-icon > img {
    height: 64px;
}

.square-splash > a > img {
    height: 36px;
    border: 2px solid rgb(223, 215, 215);
    border-radius: 8px;
}

.match-splash > img {
    height: 48px;
    border: 2px solid black;
    border-radius: 8px;
}

.matches {
    width: 100%;
    max-width: 1112px;
    margin: 50px auto 48px;
    display: flex;
    flex-direction: column;
    padding: 0px 16px;
    gap: 10px;
}

.match-container, #final-board > div {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;

}

.match-info {
    display: flex;
    align-items: center;
}


.match {
    padding: 20px;
    background-color: #28292e;
    border: 1px solid #e6e6e6;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 8px;
    height: 50px;
}

#active-traits > div {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: 224px;
}

.match-trait {
    position: relative;
    width: 36px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.match-info > div {
    width: 100%;
    margin-right: 40px;
}

#final-placement {
    width: 48px;
}

#final-stage > div {
    // font-family: 'Roboto', sans-serif;
    font-family: 'Pretendard, Thick';
    font-size: 25px;
    line-height: 1.6;
    color: #a6e2ec; 
    padding: 10px;
    font-weight: 600;
}

#final-placement > div {
    display: flex;
    font-weight: 600;
    justify-content: center;
    align-items: center;
}

#placement-1 {
    font-family: 'Pretendard, Thick';
    font-size: 30px;
    width: 38px;
    height: 38px;
    border-radius: 6px;
    color: rgb(255, 215, 0); 
    border: 2px solid rgb(218, 165, 32); 
    background: rgba(255, 215, 0, 0.1); 
}

#placement-2, #placement-3, 
#placement-4 {
    font-family: 'Pretendard, Thick';
    font-size: 30px;
    width: 38px;
    height: 38px;
    border-radius: 6px;
    color: rgb(192, 192, 192); 
    border: 2px solid rgb(169, 169, 169); 
    background: rgba(192, 192, 192, 0.1); 
}

#placement-5, #placement-6,
#placement-7, #placement-8 {
    font-family: 'Pretendard, Thick';
    font-size: 30px;
    width: 38px;
    height: 38px;
    border-radius: 6px;
    color: rgb(139, 87, 35); 
    border: 2px solid rgb(109, 69, 27); 
    background: rgba(139, 87, 35, 0.1); 
}

.match-trait > img {
    width: 22px;
    filter: invert(100%);
}

.traits, .champions {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column; 
    justify-content: center; 
    align-items: center; 
    margin: 0 auto; 
    max-width: 100%; 
    padding: 0 20px; 
    margin-bottom: 30px;
}


.traits > div, .champions > div {
    display: inline-grid;
    justify-content: center;
    grid-template-columns: repeat(4, 258px);
    row-gap: 16px;
    margin: 0 auto;
    gap: 10px;
}

.traits > div > div {
    background-color: #28292e;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    min-height: 400px;
    padding: 16px;
    border-radius: 6px;
}

.traits-champs > div {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 2%;
}

#traitDesc {
    font-family: 'Pretendard, Thin';
    margin-top: 16px;
    color: #b7bac3!important;
    font-size: 13px;
    font-weight: 400;
    line-height: 1.4;
}

.trait-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#traitName {
    font-family: 'Pretendard, Thin';
}

.traits > h2 {
    font-family: 'Pretendard, Thin';
    font-size: 30px;
    margin-top: 5%;
    margin-bottom: 20px;
    margin-left: 20px;
}

.champion-img > a > img {
    width: 258px;
}

.champ-info {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.trait-cost, .trait-cost:last-child > div {
    display: flex;
    align-items: center;
}

#traits {
    margin-right: 20px;
}

#traits > img {
    width: 20px;
}

.gold-coin {
    width: 18px;
    margin-right: 4px;
    filter: brightness(0) saturate(100%) invert(92%) sepia(58%) saturate(2719%) hue-rotate(318deg) brightness(94%) contrast(102%);
}

.champion-img {
    position: relative;

}

.champ-info {
    backdrop-filter: blur(6px);
    height: 40px;
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0px 16px;
    color: rgb(255, 255, 255);
    font-size: 16px;
    font-weight: 600;
    font-family: 'Pretendard, Thin';
    transition: all 0.25s ease 0s;
}

#img {
    border-radius: 6px;
    // border: 1px solid white;
}

.champions > h2 {
    margin-top: 24px;
    font-family: 'Pretendard, Thin';
    font-size: 30px;
    margin-bottom: 20px;
}

.champion-img > a > img:hover, .square-splash > a > img:hover,
.links > a > img:hover {
    animation: glow 1s infinite alternate;
}

@keyframes glow {
    from {
        filter: drop-shadow(0 0 5px #fff);
    }
    to {
        filter: drop-shadow(0 0 20px #007bff);
    }
}


.sticky-navbar {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    position: fixed;
    top: 30px; 
    right: 30px; 
    background-color: #333;
    padding: 10px 20px; 
    border-radius: 5px;
    z-index: 1000;
    height: 150px;
}

.sticky-navbar > a {
    margin-top: 5px;
    border: 2px solid #555; 
    border-radius: 5px; 
    padding: 5px; 
    width: 100%;
    background-color: rgb(77, 77, 77);
    text-align: center;
}

.sticky-navbar a {
    color: #fff; 
    text-decoration: none;
    margin-top: 5px;
    font-family: 'Pretendard, Thin';
    font-size: 16px;
}

.sticky-navbar a:hover, .instructions > img:hover {
    animation: glow 1s infinite alternate;
}

.links {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    width: 100%;
}

.links > a > img, .instructions > img {
    width: 24px;
}

.instructions {
    position: relative;
    display: inline-block;
}

.dropdown {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    max-width: 133px;
}

.dropdown-desc {
    position: absolute;
    background-color: #333;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    width: 200px;
    top: calc(100% + 15px); 
    left: -75px;
    transform: translateX(-50%);
    word-wrap: break-word;
    opacity: 0;
    transition: opacity 0.3s ease;
    pointer-events: none;
    font-family: 'Pretendard, Thin';
    font-size: 16px;
    text-align: center;
}


.instructions:hover .dropdown-desc {
    opacity: 1;
    pointer-events: auto;
}